/* rest of app styles */

/* Css properties that will be applied to elements in all sections*/

.overflow-wrap {
  overflow-x: hidden !important;
  font-size: 20px;
  font-family: 'Montserrat', sans-serif;
}

section {
  height: calc(100vh);
}

.flex {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

