/* Navbar Syles */

.header {
  position: fixed;
  width: 100%;
  text-align: right;
  transition: 0.25s;
  z-index: 2;
  color: black;
}

.header ul li {
  font-size: 1.25rem;
  display: inline;
  padding-right: 3rem;
  cursor: pointer;
}

.navitem > .active {
  border-bottom: 2px solid white;
}

.scrolled {
  display: block;
  position: fixed;
  color: white;
  background-color: #9844cb;
}

/* Intro Section Css properties */

.intro-background {
  background-color: #CC74FF;
  opacity: .6;  
  height: 100vh;
  width: 100%;
  position: absolute;
}

.intro-content {
  z-index: 1;
  color: white;
  font-size: 60;
}

.MePic {
  width: 150px;
  height: 150px;
  vertical-align: middle;
  align-content: center;
}

/* About Section Css properties */

.about-background {
  background-color: #008ba3;
  opacity: .6;  
  height: 100vh;
  width: 100%;
  position: absolute;
  z-index: -1;
}

.container {
  padding: 16px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.8);
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.left-card {
  height: 60vh;
  width: 40vw;
  float: left;
  margin-left: 4vw;
  margin-top: 10vh;
  padding-top: 3vh;
  background-color: white;
}

.right-card {
  height: 60vh;
  width: 40vw;
  float: right;
  margin-top: 15vh;
  margin-right: 4vw;
  padding-top: 3vh;
  background-color: white;
}

  /* Progress Bar */

.containerStyles {
  width: 75%;
  background-color: #e0e0de;
  border-radius: 50px;
  margin-left: 1.5rem;
  margin-right: .1rem;
  margin-bottom: 1rem;
  display: inline-block;
}

.fillerStyles {
  height: 100%;
  border-radius: inherit;
  transition: width 1s ease-in-out;
}

.labelStyles {
  padding: 5px 0px;
  color: black;
  font-weight: bold;
  border-radius: inherit;
  display: block;
}
 
.skills-table table {
  width: 100%;
  text-align: center;
  margin: 0 auto;
  border-collapse: collapse;
}

.skill-name {
  width: 20%;
}
