/* Projects Section Css properties */

.projects-background {
  background-color: #ffa5ff;
  opacity: .6;  
  height: 100vh;
  width: 100%;
  position: absolute;
  z-index: -1;
}

.project-cards {
  height: 100vh;
  width: 100vw;
}

.project-cards-table {
  width: 100vw;
}

table.project-cards td {
  margin: 0 !important;
  padding: 0px !important;
  align-content: center;
  align-items: center;
  justify-content: center;
  justify-items: center;
}

.project-container {
  position: relative;
  height: 35vh;
  width: 30vh;
  background-color: white;
  text-align: center;
  font-size: 1.25rem;
  cursor: pointer;
  margin: 2vh auto;
}

.projimg {
  position: absolute;
  height: 10vh;
  top: 35%;
  left: 33%;
  z-index: 1;
  transition: opacity 1s;
}


.projimg-text {
  position: absolute;
  color: black;
  height: 17vh;
  width: 16vw;
  top: 30%;
  left: 13%;
  opacity: 0;
  transition: opacity 1s;

}

.projimg:hover {
  opacity: 0;
}
.projimg:hover + .projimg-text {
  opacity: 1;
}

/* Component specific Css properties */

.App-logo {    
  height: 40vmin;
  width: 64px;
  height: 64px;
  cursor: pointer;
  vertical-align: middle;
  align-content: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    width: 64px;
    height: 64px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.sidebar {
  position: fixed;
  display: flex;
  flex-direction: column;
  top: 0;
  bottom: 0;
  left: 0;
  width: 350px;
  background:#1B1C1D;
  color: white;
  overflow-x: hidden;
  flex: 1 1;
  align-items: center;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.content {
  margin-left: 350px;
  min-width: 500px;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

